
.flexible {
  width: 100%;
  background: #ee0a3b;
  min-height: 1000px;
  position: relative;
}
.flexible header img {
    width: 10rem;
}
.flexible .content {
    font-size: 12px;
    padding: 0.26667rem 0.13333rem;
}
[data-dpr="2"] .flexible .content {
      font-size: 24px;
}
[data-dpr="3"] .flexible .content {
      font-size: 36px;
}
.flexible .content .item-content {
      width: 100%;
      background: white;
      padding-top: 0.26667rem;
      padding-bottom: 0.18667rem;
      margin-bottom: 1px;
      display: table;
      position: relative;
}
.flexible .content .item-content .item {
        display: table-cell;
        vertical-align: top;
}
.flexible .content .item-content .img {
        height: 2.34667rem;
        width: 2.34667rem;
        font-size: 0;
}
.flexible .content .item-content .img img {
          height: 100%;
}
.flexible .content .item-content .info {
        padding-left: 0.21333rem;
        padding-right: 0.25333rem;
}
.flexible .content .item-content .info .title a {
          color: black;
}
.flexible .content .item-content .info .price {
          margin-top: 0.12rem;
          margin-bottom: 0.17333rem;
}
.flexible .content .item-content .info .price span {
            display: inline-block;
            background: #ee0a3b;
            vertical-align: middle;
            border-radius: 0.02667rem;
            color: white;
            font-weight: 700;
            padding: 0.02267rem 0.06667rem 0.01067rem 0.06667rem;
            margin-right: 0.05333rem;
            font-size: 11px;
}
[data-dpr="2"] .flexible .content .item-content .info .price span {
              font-size: 22px;
}
[data-dpr="3"] .flexible .content .item-content .info .price span {
              font-size: 33px;
}
.flexible .content .item-content .info .price strong {
            display: inline-block;
            vertical-align: middle;
            font-weight: 700;
            color: #ee0a3b;
            font-size: 16px;
            margin-right: 0.16rem;
}
[data-dpr="2"] .flexible .content .item-content .info .price strong {
              font-size: 32px;
}
[data-dpr="3"] .flexible .content .item-content .info .price strong {
              font-size: 48px;
}
.flexible .content .item-content .info .price small {
            display: inline-block;
            vertical-align: middle;
            font-weight: 700;
            color: #ee0a3b;
            font-size: 12px;
}
[data-dpr="2"] .flexible .content .item-content .info .price small {
              font-size: 24px;
}
[data-dpr="3"] .flexible .content .item-content .info .price small {
              font-size: 36px;
}
.flexible .content .item-content .info .tip {
          color: #ff3600;
}
.flexible .content .item-content .info .btn {
          position: absolute;
          text-align: center;
          border-radius: 0.05333rem;
          width: 2.06667rem;
          height: 0.62667rem;
          bottom: 0.13333rem;
          line-height: 0.62667rem;
          right: 0.2rem;
          background: #ee0a3b;
          font-size: 14px;
          font-weight: 700;
          color: white;
}
[data-dpr="2"] .flexible .content .item-content .info .btn {
            font-size: 28px;
}
[data-dpr="3"] .flexible .content .item-content .info .btn {
            font-size: 42px;
}



